}

.movies {
  display: grid;
  margin: 0 auto;
  grid-template-columns: repeat(auto-fill, minmax(auto, 260px));
  grid-gap: 0.5%;
  padding: 3%;
  width: 80%;
  padding-top: 1%;
  justify-content: center;
}

.page-linkh {
  border-radius: 4px;
  background-color: rgba(255, 125, 125, 0.5);
}

