  flex-wrap: unset;
  margin: 0 auto;
  margin-top: 2%;
}

.left {
  font-size: 12px;
  color: black;
}

.pleaseinput {
  font-weight: bold;
  text-align: center;
  margin-top: 5%;
}
@media screen and (max-width: 600px) {
  .movies {
    grid-template-columns: repeat(2, minmax(auto, 150px));
    max-width: 100%;
    margin-top: 4%;
  }
}

:root {
  --text-color: #f2dcb3;
  --background-color: #1b1159;
  --accent-color: #87cbff;
}

body {
  margin: 0;
