}

.nav a {
  text-decoration: none;
  font-family: "Sansita Swashed", cursive;
  font-weight: 500;
  color: #f2dcb3;
  color: var(--text-color);
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #1b1159;
  background-color: var(--background-color);
  padding: 8px 12px;
  color: #f2dcb3;
  color: var(--text-color);
}

.navbar__logo {
  font-size: 24px;
  display: flex;
  color: #f2dcb3;
  color: var(--text-color);
}
.navbar__logo a {
  font-size: 24px;
  margin-left: 5px;
  margin-top: auto;
  margin-bottom: auto;
}

.navbar__logo img {
  width: 40px;
  height: 40px;
}

.navbar__menuhidden {
  display: flex;
  list-style: none;
  padding-left: 0;
}

.navbar__menuhidden li {
  padding: 8px 12px;
}
.navbar__menuhidden li:hover {
  background-color: #87cbff;
  background-color: var(--accent-color);
  border-radius: 4px;
}

.navbar__iconshidden {
  list-style: none;
  color: #f2f2f2;
  display: flex;
  padding-left: 0;
}

.navbar__iconshidden li {
  padding: 8px 12px;
  font-size: 18px;
}

.navbar__iconshidden li a {
  height: 70px;
  margin: -30px 0;
}

.navbar__iconsshow li a {
  height: 70px;
  margin: -30px 0;
}

.navbar__toogleBtn {
  display: none;
  position: absolute;
  right: 32px;
  top: 20.5px;
  font-size: 24px;
  color: #87cbff;
  color: var(--accent-color);
}
.Logout {
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  .navbar {
    flex-direction: column;
    align-items: flex-start;
    padding: 8px 24px;
  }

  .navbar__menushow {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }

  .navbar__menu li {
    width: 100%;
    text-align: center;
  }

  .navbar__iconsshow {
    display: flex;
    justify-content: center;
    color: #f2f2f2;
    width: 100%;
    margin-top: 5%;
  }

  .navbar__toogleBtn {
    display: flex;
    position: absolute;
    right: 32px;
    top: 20.5px;
  }
  .navbar__toogleBtn element.style {
    position: absolute;
  }

  element .navbar__toogleBtn {
    position: absolute;
  }

  .navbar__iconsshow li a {
    height: 60px;
    margin: 0 15px;
  }

  .navbar__menuhidden,
  .navbar__iconshidden {
    display: none;
  }
}

