/* GENERAL BUTTON STYLING */
.checklist button {
  background: #1b1159;
  color: #fff;
  border: none;
  position: relative;
  height: 40px;
  font-size: 1em;
  padding: 0 2em;
  cursor: pointer;
  transition: 800ms ease all;
  outline: none;
  border-radius: 1px 1px;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}
.checklist button:hover {
  background: #f2dcb3;
  color: black;
}
.checklist button:before,
.checklist button:after {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 0;
  background: #1b1159;
  transition: 400ms ease all;
}
.checklist button:after {
  right: inherit;
  top: inherit;
  left: 0;
  bottom: 0;
}
.checklist button:hover:before,
.checklist button:hover:after {
  width: 100%;
  transition: 800ms ease all;
}

.btnlocactive {
  background: #f2dcb3 !important;
  color: black !important;
  border-radius: 1px 1px;
}

.btnbractive {
  background: #f2dcb3 !important;
  color: black !important;
  border-radius: 1px 1px;
}

