/* body {
  line-height: 1;
} */

ol,
ul {
  list-style: none;
}

blockquote,
q {
  quotes: none;
}

blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}

.home table {
  border-collapse: collapse;
  border-spacing: 0;
}

.home body {
  -webkit-text-size-adjust: none;
}

.home mark {
  background-color: transparent;
  color: inherit;
}

.home input::-moz-focus-inner {
  border: 0;
  padding: 0;
}

.home input,
.home select,
.home textarea {
  -moz-appearance: none;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
}

/* Basic */

.home html {
  box-sizing: border-box;
}

.home *,
.home *:before,
.home *:after {
  box-sizing: inherit;
}

.home body {
  background: #f0f0f0;
}

.home body.is-preload *,
.home body.is-preload *:before,
.home body.is-preload *:after {
  -webkit-animation: none !important;
  animation: none !important;
  /*   -moz-transition: none !important;
  -webkit-transition: none !important;
  -ms-transition: none !important;
  transition: none !important; */
}

.home body,
.home input,
.home textarea,
.home select {
  font-family: "Source Sans Pro";
  font-weight: 300;
  color: #777;
  line-height: 1.65em;
  font-size: 15pt;
}

.home h1,
.home h2,
.home h3,
.home h4,
.home h5,
.home h6 {
  font-weight: 600;
  text-transform: uppercase;
  color: #888;
}

.home h1 a,
.home h2 a,
.home h3 a,
.home h4 a,
.home h5 a,
.home h6 a {
  color: inherit;
  text-decoration: none;
  border: 0;
}

.home h2 {
  font-size: 1.65em;
  font-weight: 400;
  letter-spacing: 4px;
  margin: 0 0 1.5em 0;
  line-height: 1.75em;
}

.home h3 {
  font-size: 1em;
  letter-spacing: 2px;
  margin: 0 0 1.25em 0;
}

.home a {
  transition: color 0.25s ease-in-out, border-bottom-color 0.25s ease-in-out;
  color: #666;
  text-decoration: none;
  border-bottom: solid 1px #ddd;
}

.home a strong {
  transition: color 0.25s ease-in-out, border-bottom-color 0.25s ease-in-out;
}

.home a:hover {
  color: #ed786a;
  border-bottom-color: rgba(255, 255, 255, 0);
}

.home a:hover strong {
  color: #ed786a;
}

.home strong,
.home b {
  font-weight: 600;
  color: #666;
}

.home em,
.home i {
  font-style: italic;
}

.home sub {
  position: relative;
  top: 0.5em;
  font-size: 0.8em;
}

.home sup {
  position: relative;
  top: -0.5em;
  font-size: 0.8em;
}

.home hr {
  border: 0;
  border-top: solid 1px #ddd;
}

.home blockquote {
  border-left: solid 0.5em #ddd;
  padding: 1em 0 1em 2em;
  font-style: italic;
}

.home p {
  text-align: justify;
  margin-bottom: 2em;
}

.home ul,
.home ol,
.home dl,
.home table,
.home blockquote {
  margin-bottom: 2em;
}

.home br.clear {
  clear: both;
}

/* Container */

.home .container {
  margin: 0 auto;
  max-width: 100%;
  width: 70em;
}

@media screen and (max-width: 1680px) {
  .home .container {
    width: 68em;
  }
}

@media screen and (max-width: 1280px) {
  .home .container {
    width: calc(100% - 80px);
  }
}

@media screen and (max-width: 980px) {
  .home .container {
    width: calc(100% - 100px);
  }
}

@media screen and (max-width: 736px) {
  .home .container {
    width: calc(100% - 40px);
  }
}

