  width: auto;
}

/* List */

.home ul {
  list-style: disc;
  padding-left: 1em;
}

.home ul li {
  padding-left: 0.5em;
  margin: 0.75em 0 0.75em 0;
}

/* .home ul li:first-child {
  margin-top: 0;
} */

.home ol {
  list-style: decimal;
  padding-left: 1em;
}

.home ol li {
  padding-left: 0.5em;
  margin: 0.75em 0 0.75em 0;
}

/* .home ol li:first-child {
  margin-top: 0;
} */

/* Links */

.home ul.links {
  list-style: none;
  padding-left: 0;
}

.home ul.links li {
  display: inline;
  border-left: solid 1px #d0d0d0;
