#banner > .container:before,
#banner > .container:after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  width: 35px;
  height: 141px;
  margin-top: -70px;
  opacity: 0.15;
}

#banner > .container:before {
  left: 0;
}

#banner > .container:after {
  transform: scaleX(-1);
  right: 0;
}

