    display: none;
  }

  /* Main */

  #main {
    padding: 4em 0;
  }

  /* Footer */

  #footer {
    padding: 4em 0;
  }

  /* Copyright */

  #copyright {
    margin-top: 2em;
    padding-top: 2em;
  }
}

/* Medium */

#navPanel,
#titleBar {
  display: none;
}

@media screen and (max-width: 980px) {
  /* Basic */

  .home html,
  .home body {
    overflow-x: hidden;
  }

  /* Header */

  #header > .container {
    padding: 10em 0 7em 0;
  }

  /* Nav */

  #nav {
    display: none;
  }

  /* Nav */

  #page-wrapper {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: transform 0.5s ease;
    padding-bottom: 1px;
  }

  #titleBar {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transition: transform 0.5s ease;
    display: block;
    height: 44px;
    left: 0;
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 10001;
  }

  #titleBar .toggle {
    text-decoration: none;
    position: absolute;
    left: 0;
    top: 0;
    width: 80px;
    height: 60px;
    border: 0;
  }

  #titleBar .toggle:before {
    -moz-osx-font-smoothing: grayscale;
    -webkit-font-smoothing: antialiased;
    display: inline-block;
    font-style: normal;
    font-feature-settings: normal;
    font-variant: normal;
    text-rendering: auto;
    line-height: 1;
    text-transform: none !important;
    font-family: "Font Awesome 5 Free";
    font-weight: 900;
  }

  #titleBar .toggle:before {
    display: inline-block;
    text-decoration: none;
    content: "\f0c9";
    display: block;
    width: 60px;
    height: 40px;
    background: rgba(232, 232, 232, 0.9);
    border-radius: 4px;
    position: absolute;
    left: 5px;
    top: 5px;
    box-shadow: 0.125em 0.125em 0 0 rgba(0, 0, 0, 0.15);
    text-align: center;
    line-height: 40px;
    font-size: 18px;
    color: #aaa;
  }

  #titleBar .toggle:active:before {
    opacity: 0.5;
  }

  #navPanel {
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    transform: translateX(-275px);
    transition: transform 0.5s ease;
    display: block;
    height: 100%;
    left: 0;
    overflow-y: auto;
    position: fixed;
    top: 0;
    width: 275px;
    z-index: 10002;
    background: #444;
    border-right: solid 2px #3c3c3c;
    font-weight: 400;
    text-transform: uppercase;
    color: #888;
    letter-spacing: 2px;
    font-size: 0.85em;
  }

  #navPanel .link {
    display: block;
    color: #ddd;
    text-decoration: none;
    height: 44px;
    line-height: 44px;
    border: 0;
    border-top: solid 1px #3c3c3c;
    padding: 0 1em 0 1em;
  }

  #navPanel .link:first-child {
    border-top: 0;
  }

  #navPanel .link.depth-0 {
    font-weight: 600;
    color: #fff;
  }

  #navPanel .indent-1 {
    display: inline-block;
    width: 1em;
  }

  #navPanel .indent-2 {
    display: inline-block;
    width: 2em;
  }

  #navPanel .indent-3 {
    display: inline-block;
    width: 3em;
  }

  #navPanel .indent-4 {
    display: inline-block;
    width: 4em;
  }

  #navPanel .indent-5 {
    display: inline-block;
    width: 5em;
  }

  #navPanel .depth-0 {
    color: #fff;
  }

  body.navPanel-visible #page-wrapper {
    transform: translateX(275px);
  }

  body.navPanel-visible #titleBar {
    transform: translateX(275px);
  }

  body.navPanel-visible #navPanel {
    transform: translateX(0);
  }

  /* Sidebar */

  #sidebar {
    padding-top: 6em;
  }
}

/* Small */

@media screen and (max-width: 736px) {
  /* Basic */

  .home body,
  .home input,
  .home textarea,
  .home select {
    font-size: 11pt;
  }

  .home h2,
  .home h3,
  .home h4,
  .home h5,
  .home h6 {
    font-size: 1.2em;
    letter-spacing: 2px;
    text-align: center;
    margin: 0 0 1em 0;
  }

  .home h2 br,
  .home h3 br,
  .home h4 br,
  .home h5 br,
  .home h6 br {
    display: none;
  }

  /* Image */

  .image.featured {
    margin: 0 0 1.5em 0;
  }

  .image.left {
    max-width: 35%;
  }

  .image.right {
    max-width: 35%;
  }

  /* Button */

  .home input[type="button"],
  .home input[type="submit"],
  .home input[type="reset"],
  .home button,
  .home .button {
    letter-spacing: 2px;
    display: block;
    padding: 1em 0 1em 0;
    width: 100%;
  }

  /* Actions */

  .home ul.actions li {
    display: block;
    margin: 1em 0 0 0;
  }

  .home ul.actions li:first-child {
    margin-top: 0;
  }

  /* Box */

  .box.excerpt header {
    text-align: center;
  }

  /* Header */

  #header > .container {
    padding: 4em 0 4em 0;
  }

  #header h1 {
    font-size: 2em;
    letter-spacing: 8px;
    line-height: 1.325em;
  }

  #header p {
    margin: 1.25em 0 0 0;
    letter-spacing: 2px;
  }

  /* Banner */

  #banner {
    padding: 2em 0;
  }

  #banner > .container {
    padding: 0;
  }

  #banner > .container:before,
  #banner > .container:after {
    display: none;
  }

  #banner p {
    font-size: 1.25em;
  }

  /* Features */

  #features {
    padding: 2em 0;
  }

  #features ul.actions {
    margin-top: 0;
  }

  /* Main */

  #main {
    padding: 2em 0;
  }

  /* Content */
  .col-8 col-12-medium {
    width: 66.66%;
  }
  .col-8 col-12-medium section,
  .col-8 col-12-medium article {
    margin: 3em 0 0 0;
    padding: 3em 0 0 0;
  }

  /* Sidebar */

  #sidebar {
    padding-top: 3em;
  }

  #sidebar > section,
  #sidebar > article {
    margin: 3em 0 0 0;
    padding: 3em 0 0 0;
  }

  /* Footer */

  #footer {
    padding: 2em 0;
  }

  #footer ul.icons {
    margin-bottom: 0;
  }

  /* Copyright */

  #copyright .links {
    margin-bottom: 0;
  }

  #copyright .links li {
    display: block;
    padding-left: 0;
    margin-left: 0;
    border-left: 0;
  }
}

